<template>
  <article class="c-centering" itemscope="" itemtype="http://schema.org/Person">
    <section class="mt-minus">
      <ul class="cards">
        <li class="card card--top mb-30 c-shadow">
          <div @click="toggleModal">
            <div class="photo photo--top" :style="{backgroundImage: 'url(' + top.image_url + ')' }"></div>
            <div class="detail">
              <p class="detail__title">{{ top.title }}</p>
              <p class="detail__text">{{ top.date }}</p>
              <p class="detail__text">{{ top.description }}</p>
              <div class="top-btn"></div>
            </div>
          </div>
          <stockr-modal @close="toggleModal" v-if="isOpen"/>
        </li>
      </ul>
    </section>

    <section class="mt-80">
      <h2 class="c-title"></h2>
      <ul class="cards">
        <li class="card mb-30 c-shadow" v-for="(project, num) in projects" :key="`project-${num}`">
          <router-link :to="project.url">
            <div class="photo" :style="{backgroundImage: 'url(' + project.image_url + ')' }"></div>
            <div class="detail">
              <p class="detail__title">{{ project.title }}</p>
              <p class="detail__text">{{ project.date }}</p>
              <p class="detail__text">{{ project.description }}</p>
            </div>
          </router-link>
        </li>
      </ul>
    </section>

    <section class="mt-80">
      <h2 class="c-title"></h2>
      <ul class="cards">
        <li class="card mb-30 c-shadow" v-for="(activity, num) in activities" :key="`activity-${num}`">
          <router-link :to="activity.url">
            <div class="photo" :style="{backgroundImage: 'url(' + activity.image_url + ')' }"></div>
            <div class="detail">
              <p class="detail__title">{{ activity.title }}</p>
              <p class="detail__text">{{ activity.date }}</p>
              <p class="detail__text">{{ activity.description }}</p>
            </div>
          </router-link>
        </li>
      </ul>
    </section>
  </article>
</template>

<script>
import StockrModal from '@/components/StockrModal'

export default {
  name: 'WorksArea',
  components: {
    StockrModal
  },
  data () {
    return {
      isOpen: false,
      top: {
        title: 'Stockr - ',
        description: '110',
        image_url: require('@/assets/image/stockr/ogp2.png'),
        date: '2020-6 - 2021-'
      },
      projects: [
        {
          title: 'BuildIt  - ',
          description: '',
          url: '/works/Build%20It',
          image_url: require('@/assets/image/bldt.jpg'),
          date: '2020-5 - 2020-6'
        },
        {
          title: ' - IA/UI',
          description: 'UI',
          url: '/works/Kamakura',
          image_url: require('@/assets/image/kamakura1.png'),
          date: '2020-4 - 2020-5'
        },
        {
          title: 'SaaSCore - UI',
          description: 'CoreSaaS',
          url: '/works/Core',
          image_url: require('@/assets/image/core.jpg'),
          date: '2019-11 - 2020-3'
        },
        {
          title: 'Lobi - ',
          description: 'AngularJSNuxt.jsWebRTC(Jest)A/B',
          url: '/works/Lobi',
          image_url: require('@/assets/image/lobi.png'),
          date: '2019-1 - 2019-8'
        },
        {
          title: 'Lobi Tournament - e-sports',
          description: '',
          url: '/works/Lobi%20Tournament',
          image_url: require('@/assets/image/lobi-tournament.png'),
          date: '2018-4 - 2018-12'
        }
      ],
      activities: [
        {
          title: '',
          description: '11',
          url: '/works/Book',
          image_url: require('@/assets/image/dezabiyo.png'),
          date: '2021-2'
        },
        {
          title: 'Chrome',
          description: 'SpottChrome',
          url: '/works/Spott',
          image_url: require('@/assets/image/spott.png'),
          date: '2020-9'
        }
      ]
    }
  },
  methods: {
    toggleModal () {
      this.isOpen = !this.isOpen
    }
  }
}
</script>

<style scoped lang="stylus">
@import "../assets/css/common.styl"

.cards
  display flex
  flex-wrap wrap
  justify-content space-between

.card
  position relative
  width calc(50% - 16px)
  min-width 320px
  background-color white
  border-radius 4px

  .detail__title
  .detail__text
    color rgb(36, 40, 42)

  &:hover
    .detail__title
    .detail__text
      opacity 0.6

  @media (max-width: 768px)
    width 100%

.photo
  float left
  width 100%
  height 24vw
  background-color #f0f3f7
  background-size cover
  background-position 50%
  border-radius 4px 4px 0 0

  @media (max-width: 768px)
    height 50vw

  @media (min-width: 1200px)
    height 288px

.detail
  padding 20px
  overflow hidden

  &__title
    line-height 1.6
    margin 0 0 10px
    font-size 20px
    font-weight 500

  &__text
    line-height 1.8
    overflow hidden
    text-overflow ellipsis

.card--top
  @media (min-width: 769px)
    width 100%

    &:hover
      .top-btn
        opacity 0.6

.photo--top
  @media (min-width: 769px)
    float right
    width calc(50% - 20px)
    margin 20px
    border-radius 4px

.top-btn
  display block
  margin-top 48px
  padding 8px 32px
  width fit-content
  border 1px solid rgb(36, 40, 42)
  border-radius 4px
  font-size 13px
  font-weight 700
  color rgb(36, 40, 42)

  @media screen and (max-width: 768px)
    display none
</style>



